

*,

*:before,

*:after{

padding: 0;

margin: 0;

box-sizing: border-box;

}
.Form_body{
margin-top: -10px;
background-color: #080710;
height: 1500px;
width: 100vw;


}

.background{
margin-top: 50px;
width: 430px;

height: 520px;

position: absolute;

transform: translate(-50%,-50%);

left: 50%;

top: 50%;

}

.background .shape{

height: 200px;

width: 200px;

position: absolute;

border-radius: 50%;

}

.shape:nth-child(1){

background: linear-gradient(

  #1845ad,

  #23a2f6

);

left: -80px;

top: -80px;

}

.shape:nth-child(2){

background: linear-gradient(

  to right,

  #ff512f,

  #f09819

);

right: -30px;

bottom: -80px;

}

.shape:nth-child(3){

    background: linear-gradient(
    
      #3dad18,
    
      #23a2f6
    
    );
    
    left: -80px;
    
    top: 830px;
    
    }

.form{
margin-top: 100px;
height:1400px;

width: 500px;

background:transparent;

position: absolute;

transform: translate(-50%,-50%);

top: 700px;

left: 50%;






border: 2px solid rgba(255,255,255,0.1);

box-shadow: 0 0 40px rgba(8,7,16,0.6);

padding: 50px 35px;

}

.form *{

font-family: 'Poppins',sans-serif;

color: #ffffff;

letter-spacing: 0.5px;

outline: none;

border: none;



}

.form h3{

font-size: 32px;

font-weight: 500;

line-height: 42px;

text-align: center;

}



label{

display: block;

margin-top: 30px;

font-size: 16px;

font-weight: 500;

}

input{

display: block;

height: 50px;

width: 100%;

background-color: rgba(255,255,255,0.07);

border-radius: 3px;

padding: 0 10px;

margin-top: 8px;

font-size: 14px;

font-weight: 300;

}
select{

    display: block;
    
    height: 50px;
    
    width: 100%;
    
    background-color: rgba(255,255,255,0.07);
    
    border-radius: 3px;
    
    padding: 0 10px;
    
    margin-top: 8px;
    
    font-size: 14px;
    
    font-weight: 300;
    
    }

option{
    background-color:black;
    color:white;
    height: 50px;
    border-radius: 3px;
}
::placeholder{

color: #e5e5e5;

}

.button{

margin-top: 20px;

width: 100%;

background-color: white;

color: #080710;

padding: 15px 0;

font-size: 18px;

font-weight: 600;

border-radius: 5px;

cursor: pointer;

}

.social{

margin-top: 30px;

display: flex;

}

.social div{

background: red;

width: 150px;

border-radius: 3px;

padding: 5px 10px 10px 5px;

background-color: rgba(255,255,255,0.27);

color: #eaf0fb;

text-align: center;

}

.social div:hover{

background-color: rgba(255,255,255,0.47);

}

.social .fb{

margin-left: 25px;

}

.social i{

margin-right: 4px;

}

input[type=radio] {
    border: 0px;
    width: 100%;
    height: 2em;
    
}
.rl{
    margin-left: 10px;
    margin-top: 14px;

}
.Radiobtns{
    display: flex;
    flex-direction: row;
    justify-content:space-evenly;
    margin-left: 10px;
}
.Radio{
    display: flex;
    flex-direction: row;
}